import React from 'react';

const WeatherData = (props) => {

    const {temp, humidity, pressure, temp_max, temp_min, icon, feels_like} = props.weather.data.main;

    return (
        <div className="weather-data">
            <p className="weather__tagline">Here's the forecast for <span className="weather-data__city">{props.weather.data.name}</span></p>

            <div className="weather-data__box">
                <span className="weather-data__property current-temp">
                <p className="weather-data__title title">Current Temperature</p>
                <p className="weather-data__value temp">{temp}</p>
                <p className="weather-data__title feels_like">Feels like {feels_like}</p>
                <p className="weather-data__icon icon">{icon}</p>
                </span>

                <span className="high-low">
                    <span className="weather-data__property high">
                    <p className="weather-data__title2">High</p>
                    <p className="weather-data__value2">{temp_max}</p>
                    </span>

                    <span className="weather-data__property low">
                    <p className="weather-data__title2">Low</p>
                    <p className="weather-data__value2">{temp_min}</p>
                    </span>
                </span>

                <span className="weather-data__property humidity">
                <p className="weather-data__value">{humidity}</p>
                <p className="weather-data__title">Humidity</p>
                </span>

                <span className="weather-data__property pressure">
                <p className="weather-data__value">{pressure}</p>
                <p className="weather-data__title">Pressure</p>
                </span>

            </div>

        </div>
    )
}

export default WeatherData;