import React from 'react';

const WeatherSearch = (props) => {
    return (
        <view className="weather-search">
            <form onSubmit={props.api_call} className="weather-search__form">
                
                
            <input name="location" placeholder="Enter your city" autoComplete="off" className="weather-search__input" type="text" />
            
            <div className="weather-search__submit">
                <button className="weather-search__button">&rarr;</button>
            </div>

            </form>
        </view>

    )
}

export default WeatherSearch;